/* Large Devices, Wide Screens */
@media only screen and (max-width : 1199px) {
  .stub {
    &__container {
      padding-left: 45px; } }
  .page404 {
    &__container {
      padding-left: 45px; } }
  .advantages {
    &__block {
      padding-left: 0;
      &-head {
        margin-left: 5px; } } }
  .directions {
    &__block {
      padding-top: 25px;
      max-width: 293px;
      &-img {
        width: 242px;
        height: 242px;
        img {
          max-width: 298px;
          max-height: 298px; } }
      &-lines {
        &>div {
          width: 70px;
          height: 70px;
          &:first-child {
            transform: translate(0) skewX(-11deg);
            &+div {
              transform: translate(48px, 0px) skewX(-11deg);
              &+div {
                transform: translate(0, 210px) skewX(-11deg);
                &+div {
                  transform: translate(190px, 90px) skewX(-11deg); } } } } } }
      &:hover {
        .directions__block {
          &-lines {
            &>div {
              &:first-child {
                transform: translate(0) skewX(-11deg);
                &+div {
                  transform: translate(48px, 60px) skewX(-11deg);
                  &+div {
                    transform: translate(110px, 210px) skewX(-11deg);
                    &+div {
                      transform: translate(190px, 142px) skewX(-11deg); } } } } } } } } } }
  .angle {
    padding-top: 45px;
    margin-top: 45px;
    min-height: 650px;
    &__wrapper {
      &-left {
        top: -45px !important;
        bottom: 45px !important;
        width: 50%;
        svg {
          width: 165px;
          right: -165px; } }
      &-right {
        width: calc(50% + 10px);
        clip-path: polygon(165px 0%, 100% 0, 100% 100%, 0% 100%); } }
    &--reverse {
      padding-top: 84px;
      min-height: 710px;
      #map_canvas {
        padding-bottom: 750px; }
      .angle__wrapper {
        &-left {
          svg {
            right: -137px;
            width: 137px; } } } } }
  .header {
    &__top {
      &-wrapper {
        justify-content: space-between; } }
    &__nav {
      &-a {
        padding: 0 15px; } } }
  .main-slider {
    &__block {
      height: 645px; }
    .slick-dots {
      bottom: 25px; } }
  .menu-pdf {
    padding-top: 140px; }
  .gallery {
    &__block {
      width: 33.3%; }
    &__row {
      .gallery__block {
        &:last-child {
          display: none; } } }
    &--list {
      .gallery__block {
        display: block !important; } } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
  .advantages {
    &__row {
      flex-direction: column;
      &>div {
        width: 100%; } }
    &__block {
      padding-left: 63px;
      margin-bottom: 30px;
      &-head {
        margin-left: -57px; } } }
  .section {
    padding: 50px 0;
    &__head {
      &-icon {
        &::before {
          left: -20px; } } } }
  .directions {
    &__block {
      padding-top: 20px;
      max-width: 220px;
      &-img {
        width: 180px;
        height: 180px;
        img {
          max-width: 222px;
          max-height: 222px; } }
      &-lines {
        &>div {
          width: 55px;
          height: 55px;
          &:first-child {
            transform: translate(0) skewX(-11deg);
            &+div {
              transform: translate(38px, 0px) skewX(-11deg);
              &+div {
                transform: translate(0, 155px) skewX(-11deg);
                &+div {
                  transform: translate(140px, 70px) skewX(-11deg); } } } } } }
      &:hover {
        .directions__block {
          &-lines {
            &>div {
              &:first-child {
                transform: translate(0) skewX(-11deg);
                &+div {
                  transform: translate(38px, 47px) skewX(-11deg);
                  &+div {
                    transform: translate(30px, 155px) skewX(-11deg);
                    &+div {
                      transform: translate(140px, 102px) skewX(-11deg); } } } } } } } } } }
  .angle {
    padding-top: 24px;
    margin-top: 75px;
    min-height: 760px;
    &__content {
      max-width: 345px; }
    &--reverse {
      padding-top: 74px;
      min-height: 730px;
      &+.section {
        padding-top: 90px; } } }
  .header {
    height: 80px;
    &-separator {
      height: 80px; }
    &__bottom {
      height: 80px;
      &>.container {
        position: relative; } }
    &__top {
      background: none;
      height: 0;
      padding: 0;
      &>.container {
        position: relative; }
      &-wrapper {
        position: absolute;
        right: 0;
        width: 345px;
        top: 35px;
        z-index: 10; } }
    &__button {
      position: absolute;
      display: block; }
    &__logo {
      padding-top: 16px;
      margin-left: 125px;
      img {
        width: 197px; } }
    &__nav {
      display: block;
      opacity: 0;
      visibility: hidden;
      width: 320px;
      border-top: 1px solid $color-green;
      position: absolute;
      float: none;
      left: 15px;
      margin-top: 80px;
      top: 0;
      background: $color-white;
      padding: 10px 0;
      box-shadow: 0 8px 12px rgba(#b5b5b5,.4);
      transition: opacity 0.35s, visibility 0.35s;
      @include clearfix;
      &.is-active {
        opacity: 1;
        visibility: visible; }
      &-ul {
        float: none; }
      &-li {
        border: 0;
        float: none; }
      &-a {
        border: 0;
        height: 45px;
        line-height: 44px;
        transform: none;
        display: block;
        &>span {
          transform: none; } }
      &-ulul {
        display: none !important; } } }
  .main-slider {
    &__block {
      height: 550px; } }
  .menu-pdf {
    &__wrapper {
      &-body {
        overflow: hidden;
        &>div {
          height: auto; }
        .btn {
          margin-top: 50px;
          margin-bottom: 300px; } } }
    &__img {
      bottom: -31px; } }
  .rental-conditions {
    .btn {
      margin-right: 25px; } }
  .how-to-use {
    &__row {
      &>div {
        width: 50%; } } } }

