      background: {
        color: $color-gray;
        size: cover;
        repeat: no-repeat;
        position: top center; } }
    &-head {
      height: 68px;
      display: flex;
      align-items: center;
      margin-left: -43px;
      margin-bottom: -8px;
      position: relative;
      z-index: 5;
      font-size: rem(20);
      font-family: $font-roboto-medium;
      &>div {
        line-height: 23px;
        &+div {
