    &-icon {
      width: 68px;
      height: 68px;
      background: $primary-color;
      color: $color-white;
      transform: skewX(-10deg);
      overflow: hidden;
      &>div {
        transform: skewX(10deg);
        height: 87px;
        position: relative;
        z-index: 5;
        overflow: hidden;
        left: 3px; }
      span {
        font-family: $font-akz-rc;
        font-size: rem(80);
        line-height: 1;
        display: block;
        position: absolute;
        right: -6px;
        bottom: -2px; } } } }

.angle {
  // background: pink
  position: relative;
  // overflow: hidden
  min-height: 980px;
  padding-top: 25px;
  margin-top: 94px;
  &__wrapper {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    &>div {
      position: absolute;
      top: 0;
      bottom: 0;
      z-index: 0; }
    &-left {
      background-color: #fbfafe;
      left: 0;
      width: calc(50% - 98px);
      z-index: 5 !important;
      top: -94px !important;
      bottom: 94px !important;
      svg {
        position: absolute;
        right: -235px;
        width: 235px;
        top: 0;
        bottom: 0;
        fill: #fbfafe; } }
    &-right {
      width: calc(50% + 120px);
      background: {
        // color: green
        position: center;
        repeat: no-repeat;
        size: cover; }
      right: 0;
      overflow: hidden;
      clip-path: polygon(235px 0%, 100% 0, 100% 100%, 0% 100%); } }
  &>.container {
    position: relative;
    z-index: 10; }
  &__content {
    max-width: 458px; }
  &--contacts {
    min-height: 793px;
    .angle__wrapper {
      &-left {
        bottom: 0 !important;
        svg {
          width: 205px;
          right: -205px; } }
      &-right {
        width: calc(50% + 98px);
        clip-path: none; } } }
  &__img {
    display: none; }
  &--reverse {
    padding-top: 120px;
    min-height: 820px;
    .angle__wrapper {
      &-left {
        top: 0 !important;
        bottom: 0 !important;
        svg {
          right: -189px;
          width: 189px; } }
      &-right {
        top: -90px !important;
        bottom: 80px;
        width: calc(50% + 80px);
        clip-path: polygon(193px 0%, 100% 0, 100% 100%, 0% 100%); } }
    #map_canvas {
      padding-bottom: 830px; } } }

.directions {
  &__row {
    flex-wrap: wrap;
    display: flex;
    margin: 0 -15px;
    &>div {
      width: 33.3%;
      padding: 0 15px; }
    &--2column {
      &>div {
        width: 50%; } } }

  &__block {
    display: block;
    text-decoration: none;
    position: relative;
    padding-top: 36px;
    max-width: 360px;
    margin: 0 auto;
    &-img {
      width: 292px;
      height: 292px;
      position: relative;
      box-shadow: 0 5px 12px rgba(#29478f,.5);
      transition: box-shadow 0.35s;
      img {
        position: absolute;
        left: 0;
        bottom: 0; } }
    &-title {
      transform: skewX(-10deg);
      position: absolute;
      bottom: -25px;
      right: 3px;
      background: $primary-color;
      color: $color-white;
      font-size: rem(25);
      font-family: $font-akz-mc;
      text-transform: uppercase;
      // filter: drop-shadow(0 0 0 rgba(#29478f,.5))
      transition: all 0.35s;
      z-index: 25;
      &>span {
        display: block;
        transform: skewX(10deg);
        height: 52px;
        line-height: 52px;
        width: 147px;
        text-align: center;
 } }        // filter: none
    &-lines {
      &>div {
        position: absolute;
        background: rgba($primary-color,.7);
        height: 88px;
        width: 88px;
        transform: skewX(-11deg);
        z-index: 10;
        top: 0;
        left: 8px;
        transition: all 0.35s;
        &:first-child {
          transform: translate(0) skewX(-11deg);
          opacity: 0;
          &+div {
            // transform: translate(58px, 50px) skewX(-15deg)
            transform: translate(58px, 0px) skewX(-11deg);
            &+div {
              transform: translate(0, 256px) skewX(-11deg);
              &+div {
                transform: translate(250px, 90px) skewX(-11deg); } } } } } }
    &--yellow {
      .directions__block {
        &-title {
          background: $color-yellow; }
        &-lines {
          &>div {
            background: rgba($color-yellow,.7); } } } }
    &--green {
      .directions__block {
        &-title {
          background: $color-green; }
        &-lines {
          &>div {
            background: rgba($color-green,.7); } } } }
    &--blue {
      .directions__block {
        &-title {
          background: $color-blue; }
        &-lines {
          &>div {
            background: rgba($color-blue,.7); } } } }
    &:hover {
      .directions__block {
        &-img {
          box-shadow: 0 0 12px rgba(#3e3b33,.5); }
        &-title {
          background: #d6060d; }
        &-lines {
          &>div {
            background: rgba($color-red,.7);
            &:first-child {
              opacity: 1;
              transform: translate(0) skewX(-11deg);
              &+div {
                transform: translate(58px, 60px) skewX(-11deg);
                &+div {
                  transform: translate(148px, 256px) skewX(-11deg);
                  &+div {
                    transform: translate(250px, 168px) skewX(-11deg); } } } } } } } } } }

