#map_canvas {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 0;
  width: auto;
  padding-bottom: 793px;
  iframe {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0; } }

.main-slider {
  overflow: hidden;
  position: relative;
  &__carousel {
    outline: none;
    &:focus {
      outline: none; } }
  .slick-dots {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0;
    padding: 0;
    text-align: center;
    height: 12px;
    line-height: 1;
    bottom: 41px;
    padding-left: 113px;
    &>li {
      list-style: none;
      margin: 0;
      display: inline-block;
      vertical-align: 0;
      line-height: 1;
      &:first-child {
        margin-left: 160px; }
      button {
        background-color: transparent;
        border: 1px solid $color-white;
        border-radius: 50%;
        height: 12px;
        width: 12px;
        font-size: 0;
        padding: 0;
        margin: 0 5px;
        position: relative;
        z-index: 150; }
      &.slick-active {
        button {
          background: $color-white; } } } }
  &__block {
    height: 830px;
    background: {
      position: top center;
      size: cover;
      repeat: no-repeat; }

    img {
      max-width: 100%;
      height: auto;
      display: block; } } }

.mouse {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 100;
  bottom: 15px;
  color: $color-white;
  &>div, &>a {
    text-decoration: none;
    display: inline-block;
    position: relative;
    left: -10px;
    cursor: pointer;
    img {
      animation: sdb05 3.5s infinite; } } }

@keyframes sdb05 {
  0% {
    transform: rotate(0) translate(0, 0);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    transform: rotate(0) translate(0, 10px);
    opacity: 0; } }

.breadcrumbs {
  min-height: 90px;
  font-size: rem(15);
  color: $primary-color;
  padding: 35px 0 25px 0;
