        &::before {
          content: '/';
          display: inline-block;
          margin-left: 5px;
          margin-right: 5px; } }
      a, span {
        display: inline-block;
        color: $primary-color; }
      .fa-home {
        margin-right: 5px;
        display: inline-block; } } }
  &+.angle--reverse {
    margin-top: 0px; } }

