.gallery {
  &__row {
    @include clearfix; }
  &__block {
    position: relative;
    float: left;
    overflow: hidden;
    width: 25%;
    &>a {
      display: block;
      &::after {
        content: '';
        background: $color-yellow;
        opacity: 0;
        transition: opacity 0.35s;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 10; } }
    img {
      max-width: 100%;
      height: auto;
      display: block; }
    &:hover {
      &>a {
        &::after {
          opacity: 0.5; }
        &::before {
          opacity: 1; } } } }
  &-item {
    &::before {
      opacity: 0;
      transition: opacity 0.35s;
      content: "\f00e";
      font-family: $font-awesome;
      width: 50px;
      height: 50px;
      background: rgba($color-white,.55);
      color: $color-yellow;
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      font-size: rem(25);
      text-align: center;
      line-height: 50px;
      z-index: 15; } }
  &--list {
    .gallery__block {
      width: 100%;
      margin-bottom: 30px; }
    .pagination {
      padding-top: 50px; } } }

